import Scrollbar from 'smooth-scrollbar';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

// ==== Smooth Scrollbar Initialization ====
let scrollbar;
if (document.querySelector('#my-scrollbar')) {
  scrollbar = Scrollbar.init(document.querySelector('#my-scrollbar'), {
    damping: 0.04,
    alwaysShowTracks: true,
    continuousScrolling: false,
  });
}

// ==== Anchor Links for Smooth Scrollbar ====
document.querySelectorAll('a[href^="#"]').forEach(anchor => {
  anchor.addEventListener('click', function(e) {
    e.preventDefault();
    const target = document.querySelector(this.getAttribute('href'));
    if (target && scrollbar) {
      const offsetTop = target.offsetTop;
      scrollbar.scrollTo(0, offsetTop, 600); // 600ms duration
    }
  });
});

// ==== Utility Functions ====
function showAlert(message, isSuccess = false) {
  const alertBox = document.createElement('div');
  alertBox.className = `alert alert-${isSuccess ? 'success' : 'danger'} position-fixed top-0 start-0 end-0 m-3`;
  alertBox.innerHTML = `
    <div class="d-flex justify-content-between align-items-center">
      <span>${message}</span>
      <button type="button" class="btn-close" data-bs-dismiss="alert"></button>
    </div>
  `;
  document.body.prepend(alertBox);
  setTimeout(() => {
    alertBox.classList.add('fade');
    setTimeout(() => alertBox.remove(), 150);
  }, 5000);
}

function validateEmail(email) {
  return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
}

function setLoadingState(form, isLoading) {
  const submitBtn = form.querySelector('button[type="submit"]');
  if (submitBtn) {
    submitBtn.disabled = isLoading;
    if (isLoading) {
      submitBtn.dataset.originalText = submitBtn.innerHTML;
      submitBtn.innerHTML = '<span class="spinner-border spinner-border-sm"></span> Processing...';
    } else {
      submitBtn.innerHTML = submitBtn.dataset.originalText || 'Submit';
    }
  }
}

// ==== Main Application Logic ====
document.addEventListener("DOMContentLoaded", () => {
  const API_BASE_URL = "https://api-verifier.onrender.com";
  const FRONTEND_BASE_URL = "https://quader864.github.io";

  // Preserve original button text
  document.querySelectorAll('button[type="submit"]').forEach(btn => {
    btn.dataset.originalText = btn.innerHTML;
  });

  // ==== Login Form ====
  const loginForm = document.getElementById("loginForm");
  if (loginForm) {
    loginForm.addEventListener("submit", async e => {
      e.preventDefault();
      setLoadingState(loginForm, true);

      const email = document.getElementById("loginEmail").value.trim();
      const password = document.getElementById("loginPassword").value;

      if (!email || !password) {
        showAlert("Both email and password are required", false);
        setLoadingState(loginForm, false);
        return;
      }

      if (!validateEmail(email)) {
        showAlert("Please enter a valid email address", false);
        setLoadingState(loginForm, false);
        return;
      }

      try {
        const response = await fetch(`${API_BASE_URL}/login`, {
          method: "POST",
          credentials: "include",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ email, password })
        });
        const data = await response.json();

        if (response.ok) {
          showAlert("Login successful! Redirecting...", true);
          setTimeout(() => window.location.href = `${FRONTEND_BASE_URL}/profile.html`, 1500);
        } else {
          showAlert(data.message || "Login failed", false);
        }
      } catch (err) {
        console.error(err);
        showAlert("Network error. Please try again.", false);
      } finally {
        setLoadingState(loginForm, false);
      }
    });
  }

  // ==== Signup Form ====
  const signupForm = document.getElementById("signupForm");
  if (signupForm) {
    signupForm.addEventListener("submit", async e => {
      e.preventDefault();
      setLoadingState(signupForm, true);

      const firstName = document.getElementById("firstName").value.trim();
      const lastName = document.getElementById("lastName").value.trim();
      const email = document.getElementById("email").value.trim();
      const password = document.getElementById("password").value;

      if (!firstName || !lastName || !email || !password) {
        showAlert("All fields are required", false);
        setLoadingState(signupForm, false);
        return;
      }
      if (!validateEmail(email)) {
        showAlert("Please enter a valid email", false);
        setLoadingState(signupForm, false);
        return;
      }
      if (password.length < 8) {
        showAlert("Password must be at least 8 characters", false);
        setLoadingState(signupForm, false);
        return;
      }

      try {
        const response = await fetch(`${API_BASE_URL}/web-register`, {
          method: "POST",
          credentials: "include",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ firstName, lastName, email, password })
        });
        const data = await response.json();

        if (response.status === 201) {
          showAlert("Account created successfully! Redirecting...", true);
          setTimeout(() => window.location.href = `${FRONTEND_BASE_URL}/profile.html`, 1500);
        } else {
          showAlert(data.message || "Registration failed", false);
        }
      } catch (err) {
        console.error(err);
        showAlert("Network error. Please try again.", false);
      } finally {
        setLoadingState(signupForm, false);
      }
    });
  }

  // ==== Logout ====
  const logoutBtn = document.getElementById("logoutBtn");
  if (logoutBtn) {
    logoutBtn.addEventListener("click", async () => {
      try {
        const response = await fetch(`${API_BASE_URL}/logout`, { method: "GET", credentials: "include" });
        if (response.ok) {
          showAlert("Logged out successfully", true);
          setTimeout(() => window.location.href = `${FRONTEND_BASE_URL}/index.html`, 1000);
        } else {
          showAlert("Logout failed", false);
        }
      } catch (err) {
        console.error(err);
        showAlert("Network error during logout", false);
      }
    });
  }

  // ==== Session Verification ====
  if (document.querySelector('.protected-page')) verifySession();

  async function verifySession() {
    try {
      const response = await fetch(`${API_BASE_URL}/verify`, { method: "POST", credentials: "include" });
      const data = await response.json();
      if (!response.ok || data.status !== "authorized") {
        window.location.href = `${FRONTEND_BASE_URL}/login.html`;
      }
    } catch (err) {
      console.error(err);
      window.location.href = `${FRONTEND_BASE_URL}/login.html`;
    }
  }

  // ==== Smooth Scrolling for Anchor Links ====
  document.querySelectorAll('a[href^="#"]').forEach(anchor => {
    anchor.addEventListener('click', e => {
      e.preventDefault();
      const target = document.getElementById(anchor.getAttribute('href').substring(1));
      if (target) {
        window.scrollTo({ top: target.offsetTop, behavior: 'smooth' });
      }
    });
  });
});
